* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

h1 {
  padding: 0;
  margin: 3px;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 240px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 270px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
  }
  
  /* Slide */
   .hover img {
      margin-left: 10px;
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      transition: .3s ease-in-out;
  }
  .hover img:hover  {
      margin-left: -30px;
  
  }
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
  
  }
.gameScore {
    text-align: center;
    height: 70px;
    position: relative;
    line-height: 1em;
    padding: 0;
    margin: 0;
  }
  div.gameScore {
  
  }
  
  h3.score {
    margin-top: 5px;
    padding: 0;
    font-weight: 600;
  }
  h3.status{
    margin: 0;
    padding: 0;
    color: red;
  }

.App-header {
  background-color: #222;
  height: 80px;
  padding: 10px;
  color: white;
}

.App-title {
  font-size: 2em;
}

.App-intro {
  font-size: large;
}
